.aside-link {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  // width: 447px;
  width: 386px;
  // .o;
  padding-top: 7px;
  padding-bottom: 7px;

  font-family: @blaster;
  font-size: 27px;
  text-align: center;

  color: #fff;
  background-color: @dark-gray;

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;

    // background-color: @dark-gray;
    width: 30px;
    height: 30px;
    // border: 1px solid red;

  }

  &::before {
    left: -35px;
    height: 0;
    width: 0;

    // border-bottom: 40px solid transparent;
    // border-right: 50px solid @dark-gray;

    border-bottom: 45px solid transparent;
    border-left: 0px solid transparent;
    border-right: 35px solid @dark-gray;


// margin: -5px 0 5px 0;
// margin: 0px -5px 5px 5px;
// margin-top: 10px;

// box-shadow: -1px 0px 0 0 #FEC712;
// border-bottom: 1px solid red;
// border-right: 0;
// border-top: 0;
// border-bottom: 0;
  }
  &::after {
    top: 39px;
    left: -6px;
    width: 6px;
    height: 2px;
    transform: rotate(20deg);
    // border-radius: 0 0 0px 40px;
    border-bottom-left-radius: 20px 10px;
    border: 1px solid @dark-gray;
    background-color: @dark-gray;
    // @dark-gray
  }
}

